import neilModal from "../../components/neil-modal/neil-modal.vue";
export default {
  components: {
    neilModal
  },
  data() {
    return {
      background: ["color1", "color2", "color3"],
      indicatorDots: true,
      autoplay: false,
      //  用户协议框的显示隐藏
      show2: false
    };
  },
  methods: {
    modalTap: function(e) {
	  //   展示用户协议框
	  this.show2 = true
	},
	getuserinfo() {
		console.log('123')
	},
	cancelAuth(){
		this.show2 = false
	},
	getAuth(){
		wx.getUserInfo({
			success() {
				console.log('获取信息成功')
			},
			error(){
				console.log('获取信息失败')
			}
		})
	}
  }
};
